:root{--offset:8px;overflow:hidden}body{display:flex;justify-content:center;align-items:center;min-height:100vh}h2{width:450px;font-size:60px;font-family:Helvetica;line-height:1}h2 .mask{height:60px;overflow:hidden;position:relative;margin-top:var(--offset)}h2 .mask span{display:block;box-sizing:border-box;position:absolute;top:60px;padding-bottom:var(--offset);background-size:100% 100%;-webkit-background-clip:text;background-clip:text;-webkit-text-fill-color:transparent;background-repeat:no-repeat}h2 .mask span[data-show]{transform:translateY(-100%);transition:transform 0.5s ease-in-out}h2 .mask span[data-up]{transform:translateY(-200%);transition:transform 0.5s ease-in-out}h2 .mask span:nth-of-type(1){background-image:linear-gradient(to right, #32b9f9 0%, #0684bf 100%)}h2 .mask span:nth-of-type(2){background-image:linear-gradient(to right, #14ee74 0%, #0b9146 100%)}h2 .mask span:nth-of-type(3){background-image:linear-gradient(to right, #7b53ba 0%, #4c2f78 100%)}h2 .mask span:nth-of-type(4){background-image:linear-gradient(to right, #f78fa5 0%, #f12f59 100%)}
